.transfer-asset-modal .modal-backdrop {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
}
.transfer-asset-modal .modal-content {
  max-height: 90vh;
  overflow-y: auto;
}
.transfer-asset-modal .form-select,
.transfer-asset-modal .form-input,
.transfer-asset-modal .form-textarea {
  transition: all 0.2s ease-in-out;
}
.transfer-asset-modal .form-select:focus,
.transfer-asset-modal .form-input:focus,
.transfer-asset-modal .form-textarea:focus {
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.transfer-asset-modal .form-select:disabled,
.transfer-asset-modal .form-input:disabled,
.transfer-asset-modal .form-textarea:disabled {
  background-color: #f9fafb;
  cursor: not-allowed;
}
.transfer-asset-modal select {
  background-image: none !important;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.transfer-asset-modal select::-ms-expand {
  display: none;
}
.transfer-asset-modal select option {
  padding: 8px 12px;
  background-color: white;
  color: #374151;
}
.transfer-asset-modal select option:hover {
  background-color: #f3f4f6;
}
.transfer-asset-modal select option:disabled {
  color: #9ca3af;
  background-color: #f9fafb;
}
.transfer-asset-modal .flatpickr-calendar {
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  border: 1px solid #e5e7eb;
}
.transfer-asset-modal .flatpickr-calendar .flatpickr-day {
  border-radius: 4px;
}
.transfer-asset-modal .flatpickr-calendar .flatpickr-day.selected {
  background-color: #3b82f6;
  border-color: #3b82f6;
}
.transfer-asset-modal .flatpickr-calendar .flatpickr-day:hover {
  background-color: #f3f4f6;
}
.transfer-asset-modal .transfer-arrow {
  animation: bounce 2s infinite;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}
.transfer-asset-modal .loading-spinner {
  animation: spin 1s linear infinite;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.transfer-asset-modal .error-message {
  animation: slideIn 0.3s ease-out;
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 640px) {
  .transfer-asset-modal .modal-content {
    margin: 1rem;
    max-width: calc(100vw - 2rem);
  }
}
